import Boxlistcity from './Boxlistcity';
import { v4 as uuidv4 } from 'uuid';

const DetailCity=()=>{

  const data = [{Brand:"Honda",Model:"City",Detail:"ครั้งแรกกับขุมพลังเทอร์โบใหม่ 1.0 ลิตร 122 แรงม้า ด้วยแรงบิดสูงสุด 173 นิวตัน-เมตร และแรงบิดเทียบเท่าเครื่องยนต์ขนาด 1.8 ลิตร แต่ยังประหยัดน้ำมันสูงถึง 23.8 กม./ลิตร",Price:"629,000"},
            
               ]

    return (
      <div >
          {data.map((elemant)=>{
          return <Boxlistcity {...elemant} key={uuidv4()}/>    
          })}
        
      </div>
    );
  }

  export default DetailCity;