import BoxlistAlmera from './BoxlistAlmera';
import { v4 as uuidv4 } from 'uuid';

const DetailAlmera=()=>{

  const data = [{Brand:"Nissan",Model:"Almera",Detail:"ตอบโจทย์ทุกบทบาท ทุกจุดหมาย ด้วยเครื่องยนต์ 1.0L TURBO ผสานเทคโนโลยีเหนือระดับ ให้คุณสะดวกสบายทุกการเดินทาง การันตีความคุ้มค่าด้วยรางวัล Car of the Year 2023* ในรุ่นปี 2022",Price:"589,000"},
            
               ]

    return (
      <div >
          {data.map((elemant)=>{
          return <BoxlistAlmera {...elemant} key={uuidv4()}/>    
          })}
        
      </div>
    );
  }

  export default DetailAlmera;