import { v4 as uuidv4 } from 'uuid';
import BoxlistAttrage from './BoxlistAttrage';

const DetailAttrage=()=>{

  const data = [{Brand:"Mitsubishi",Model:"Attrage",Detail:"ห้องโดยสารดีไซน์ใหม่ สไตล์สปอร์ต กว้างกว่า ใช้ชีวิตได้เต็มกว่าที่เคย เบาะหนังสังเคราะห์ดีไซน์พิเศษและหัวเกียร์หุ้มหนัง* พร้อมภายในตกแต่งวัสดุแบบ Piano Black และ Carbon Print",Price:"494,000"},
            
               ]

    return (
      <div >
          {data.map((elemant)=>{
          return <BoxlistAttrage {...elemant} key={uuidv4()}/>    
          })}
        
      </div>
    );
  }

  export default DetailAttrage;