import Carousel from 'react-bootstrap/Carousel';
import slide1 from './image/Yaris/Premium Luxury.webp';
import slide2 from './image/Yaris/1.webp';
import slide3 from './image/Yaris/2.webp';
import slide4 from './image/Yaris/3.webp';
import slide5 from './image/Yaris/4.webp';
import slide6 from './image/Yaris/5.jpg';
import slide7 from './image/Yaris/6.jpg';
import slide8 from './image/Yaris/7.jpg';
import slide9 from './image/Yaris/8.jpg';
import slidex from './image/Yaris/9.jpg'
import './Nav.css';

function Yarispic() {

  return (     
                <Carousel className='ali'>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide1}
                      alt="First slide"
                    />
                    <img
                      className="d-block w-100"
                      src={slide2}
                      alt="First slide"
                    />
                    <Carousel.Caption>
                      <h2>ALL NEW YARIS ATIV</h2>
                      <p>ชุดไฟหน้าแบบ Full LED พร้อมไฟส่องสว่างเวลากลางวันแบบ LED Light Guiding</p>
                    </Carousel.Caption>
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide3}
                      alt="Second slide"
                    />
                    <img
                      className="d-block w-100"
                      src={slide4}
                      alt="Second slide"
                    />

                    <Carousel.Caption>
                      <h2>Vortex Generator ช่องดักอากาศที่ช่วยเพิ่ม Aerodynamics</h2>
                      <p>กระจังหน้าสีดำเงาดีไซน์ใหม่</p>
                    </Carousel.Caption>
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide5}
                      alt="Third slide"
                    />
                    <img
                      className="d-block w-100"
                      src={slide6}
                      alt="Third slide"
                    />

                    <Carousel.Caption>
                      <h2>ไฟท้ายแบบ LED และไฟเลี้ยว LED แบบ Sequential</h2>
                      <p>
                        ไฟสร้างบรรยากาศภายในห้องโดยสาร (Ambient Light) ปรับได้ 64 เฉดสี
                      </p>
                    </Carousel.Caption>
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide7}
                      alt="Forth slide"
                      
                    />
                    <img
                      className="d-block w-100"
                      src={slide8}
                      alt="Forth slide"
                      
                    />

                    <Carousel.Caption>
                      <h3>เบาะหนังแท้และหนังสังเคราะห์สีแดง</h3>
                      <p>
                        คอลโซลหน้าหุ้มหนังสังเคราะห์สีแดง พร้อมบุนุ่ม และตกแต่งด้วยแถบสีเงินเมทัลลิก
                      </p>
                    </Carousel.Caption>
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide9}
                      alt="Five slide"
                    />
                    <img
                      className="d-block w-100"
                      src={slidex}
                      alt="Five slide"
                    />

                    <Carousel.Caption>
                      <h3>ระบบช่วยเตือนขณะถอยรถ RCTA</h3>
                      <p>
                        สัญญานเตือนกะระยะด้านหน้าและด้านหลัง
                      </p>
                    </Carousel.Caption>
                  </Carousel.Item>
                </Carousel>

  );
}

export default Yarispic;