
import { v4 as uuidv4 } from 'uuid';
import BoxlistSwift from './BoxlistSwift';

const DetailSwift=()=>{

  const data = [{Brand:"Suzuki",Model:"Swift",Detail:"POWER YOU UP กับทุกความรู้สึก ให้ใจคุณเต้นแรงกว่าเดิม ด้วยดีไซน์อันเป็นเอกลักษณ์ของ  SUZUKI SWIFT พร้อมสปีดเต็มสมรรถนะ และประหยัดน้ำมัน",Price:"567,000"}
            
               ]

    return (
      <div >
          {data.map((elemant)=>{
          return <BoxlistSwift {...elemant} key={uuidv4()}/>    
          })}
        
      </div>
    );
  }

  export default DetailSwift;