import BoxlistYaris from './BoxlistYaris';
import { v4 as uuidv4 } from 'uuid';


const DetailYaris=()=>{

  const data = [{Brand:"Toyota",Model:"Yaris",Detail:"มิติใหม่แห่งการเป็นเจ้าของ..เอกลักษณ์การออกแบบ Fastback Design สุดปราดเปรียว ผสานความสปอร์ตทรงพลัง ที่มาพร้อมชุดแต่งหลากหลายสไตล์ให้คุณเลือกได้",Price:"549,000"},
               
              ]
              return (
                 
                          <div >
                              {data.map((elemant)=>{

                              return  <BoxlistYaris {...elemant} key={uuidv4()}/>

                              })}
                          
                          </div>
                    

    );
  }

  export default DetailYaris;