import Carousel from 'react-bootstrap/Carousel';
import slide1 from './image/Almera/Almera.jpg';
import slide2 from './image/Almera/1.jpg';
import slide3 from './image/Almera/2.jpg';
import slide4 from './image/Almera/3.jpg';
import slide5 from './image/Almera/4.jpg';
import slide6 from './image/Almera/5.jpg';
import './Nav.css';

function Almerapic() {

  return (     
                <Carousel className='ali'>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide1}
                      alt="First slide"
                    />
                    <img
                      className="d-block w-100"
                      src={slide2}
                      alt="First slide"
                    />
                    <Carousel.Caption>
                      <h1>พลิกโฉมความเป็นผู้นำด้วยดีไซน์ใหม่</h1>
                      <p>นิสสัน อัลเมร่า ใหม่ พร้อมสีใหม่ Gray Sky Pearl ดีไซน์ภายนอกโมเดิร์น เท่ล้ำ สะท้อนภาพลักษณ์ใหม่ ห้องโดยสารสปอร์ตทุกมุมมอง ตอบโจทย์ทุกไลฟ์สไตล์ ทั้งคอนโซลสีน้ำเงินเข้มและที่นั่งดีไซน์สปอร์ต รับกับแบรนด์โลโก้ใหม่บนพวงมาลัย</p>
                    </Carousel.Caption>
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide3}
                      alt="Second slide"
                    />
                    <img
                      className="d-block w-100"
                      src={slide4}
                      alt="Second slide"
                    />

                    <Carousel.Caption>
                      <h3>Second slide label</h3>
                      <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
                    </Carousel.Caption>
                  </Carousel.Item>
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={slide5}
                      alt="Third slide"
                    />
                    <img
                      className="d-block w-100"
                      src={slide6}
                      alt="Third slide"
                    />

                    <Carousel.Caption>
                      <h3>Third slide label</h3>
                      <p>
                        Praesent commodo cursus magna, vel scelerisque nisl consectetur.
                      </p>
                    </Carousel.Caption>
                  </Carousel.Item>
                </Carousel>

  );
}

export default Almerapic;